<template>
    <canvas ref="photo" style="width:100%;" :width="width" :height="height"></canvas>
</template>

<script>
import loadImage from './loadImage';
import Photo from '../../../packages/photo/dist/index.es';
export default {
    name: 'Crop',

    props: {
        overlay: {
            type: Array,
            default: () => [],
        },

        width: {
            type: Number,
            default: 300,
        },
        height: {
            type: Number,
            default: 300,
        },
        img: {
            type: Image,
        },

        scale: {
            type: Number,
            default: 1,
        },

        org: {
            type: Array,
            default: () => [0, 0],
        },

        angle: {
            type: Number,
            default: 0,
        },

        // 平移距离
        offset: {
            type: Array,
            default: () => [0, 0],
        },
    },

    data() {
        return {
            photo: null,
        };
    },

    watch: {
        img(img) {
            this.photo.changeImg(img);
            this.photo.changeOrg(this.org);
        },

        overlay(overlay) {
            const p = [];
            for (const url of overlay) {
                p.push(loadImage(url));
            }
            Promise.all(p).then((imgs) => {
                this.photo.changeOverlay(...imgs);
            });
        },

        /**
         * 任何操作都会影响offset,
         * 所以render放在这里
         */
        offset: {
            deep: true,
            handler(offset, prevOffset) {
                const dx = Number(offset[0]) - Number(prevOffset[0]);
                const dy = Number(offset[1]) - Number(prevOffset[1]);
                this.photo.move(dx, dy);
            },
        },

        scale(scale) {
            this.photo.zoom(scale);
        },

        org: {
            deep: true,
            handler(org) {
                this.photo.changeOrg(org);
            },
        },

        angle(angle) {
            this.photo.rotate(angle);
        },
    },

    mounted() {
        // this.context = this.$el.getContext('2d');
        this.photo = new Photo(this.$el, this.width, this.height);
    },

    methods: {
        reset(){
            this.photo.reset();
        }
    },
};
</script>

<style scope lang="scss">
.crop-panel {
    width: 100%;
    display: block;
    margin: auto;
}
</style>